import uniInput from '../../components/xiaowu-input/clear-input.vue'
// 第三方登录组件
import thirds from '../../components/login/thirdparty.vue'
export default {
	name: "",
	components: {
		uniInput,
		thirds
	},
	props: {},
	data() {
		return {
			// 用户名
			username: '',
			// 密码
			password: ''
		}
	},
	methods: {
		// 用户名输入框
		changeName(e) {
			this.username = e.value
		},
		// 密码输入框
		changePass(e) {
			this.password = e.value
		},
		login() {
			if (this.username === '' || this.password === '') {
				uni.showToast({
					title: '请输入完整信息',
					icon: "none"
				});
			} else {
				uni.showLoading({
					title: '登录中...'
				});
				uni.request({
					url: this.$api.login(),
					method: 'POST',
					data: {
						username: this.username,
						password: this.password
					},
					success: res => {
						uni.setStorageSync('userinfo', JSON.stringify(res.data.data))
						uni.setStorageSync('userToken', res.data.data.userUniqueToken)
						this.$store.state.userObj = res.data.data
						uni.switchTab({
							url: `../my/my`,
							complete() {
								uni.hideLoading()
							}
						})
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	},
	mounted() {

	},
	onLoad() {

	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
